.title {
  padding: 30px 0px;
  color: #ffffff;
  font-family: "InterBold" !important;
  font-size: 4rem;
  line-height: 133.1%;
  white-space: pre-line;
  text-align: center;
  margin: 0;
}

.subtitle {
  font-family: "InterRegular";
  color: white;
  font-size: 22px;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}

.container {
  gap: 50px;
  padding-bottom: 24px;
  display: inline-flex;
}

@media screen and ( max-width: 600px ) { 
  .title {
    font-size: 3rem;
  }  
}