.container {
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 30px;
}

.addUserContainer {
  text-align: right;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.usersCount {
  display: flex;
}

.usersCount > div {
  display: grid;
  text-align: center;
}

.usersCount > div:not(:last-child) {
  margin-right: 20px;
}