.iconsContainer {
  display: grid;
  gap: 10px;
  margin-top: 10px;
}

.iconsContainerHorizontal {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin-top: 10px;
}

.iconContainer {
  display: flex;
  text-align: center;
  gap: 10px;
  cursor: pointer;
  padding: 10px;
}

.iconContainerHorizontal {
  display: grid;
}

.iconContainer:hover {
  background: #9829db;
  padding: 10px;
  border-radius: 10px;
}

.icon {
  width: 20px;
}

.iconHorizontal {
  margin: auto;
}

.label {
  cursor: pointer;
}