.container {
  color: white;
  max-height: calc(100vh - 200px);
  overflow-y: scroll;
  padding-right: 20px;
}

.questionContainer {
  display: grid;
  grid-template-columns: 1fr 1fr min-content;
  gap: 20px;
}

.questionsContainer {
  display: grid;
  gap: 20px;
}

.sectionContainer {
  gap: 20px;
  display: grid;
}

.questionPoints {
  margin: auto;
}

.divider {
  margin: 10px 0px;
}

.title {
  margin-bottom: 10px;
}

.buttonsContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 10px;
}

.sendButton {
  display: flex;
  justify-content: flex-end;
}
