.container {
  max-width: 425px;
  position: relative;
  background: radial-gradient(
    94.36% 94.36% at 9.43% 5.64%,
    #5164a9 2.89%,
    #5042a6 38.54%,
    #6e51af 67.19%,
    #4a65a9 100%
  );
  padding: 2px;
}

.container:hover {
  background: radial-gradient(
    94.36% 94.36% at 9.43% 5.64%,
    #9344ae 2.89%,
    #771f79 38.54%,
    #88226b 67.19%,
    #da7e4a 100%
  );
  cursor: pointer;
  padding: 8px;
}

.container,
.content {
  border-radius: 10px;
}

.content {
  background: #2a1c48;
  color: white;
  padding: 40px 50px;
}

.title {
  font-weight: 700;
}

.subtitle {
  font-weight: 400;
}

.title,
.subtitle {
  font-family: "Inter";
  font-style: normal;
  color: #ffffff;
  text-align: center;
  letter-spacing: -0.03em;
  font-size: 20px;
  line-height: 133.1%;
}

.image {
  width: 100%;
  border-radius: 10px;
}
