.protonCard {
  justify-self: center;
  align-self: center;
  width: 240px;
  height: 240px;
  border: 1px solid black;
  border-radius: 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 19px;
  background-image: linear-gradient(160deg, rgb(16, 134, 232), rgb(184, 0, 230));
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.protonCardDate {
  font-family: "Inter", sans-serif;
  color: white;
  align-self: flex-end;
  margin-top: 0;
}
.protonCardBigImages {
  display: flex;
  align-items: center;
  gap: 10px;
}
.protonCardBigImages .bigAvatar {
  width: 60px;
  height: 60px;
  background-color: grey;
}

.protonCardBigImages img {
  width: 88px;
  height: 88px;
  display: inline-block;
  border-radius: 50%;
}
.protonCardDescription {
  font-family: "Inter", sans-serif;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  margin: 0px;
}

.protonCardFooter p {
  font-family: "Inter", sans-serif;
  margin: 12px 0 0 0;
}
