.pricingContainer {
  display: flex;
  flex-wrap: wrap;
  max-width: 900px;
  margin: 0 auto;
  gap: 50px;
  justify-content: center;
}

.plan {
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 40px;
  text-align: center;
  width: calc(33.33% - 20px);
  margin-bottom: 40px;
  position: relative;
  transition: all 0.3s ease-in-out;
}

.plan:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.planName {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 10px;
}

.planPrice {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 10px;
}

.planMode {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
}

.planFeatures {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: left;
  height: 340px;
}

.planFeatures li {
  font-size: 16px;
  margin-bottom: 10px;
}

.rocket {
  width: 24px;
}

@media (max-width: 767px) {
  .plan {
    width: 100%;
    margin-right: 0;
    margin-left: 0;
  }
}
