.loadMore {
  margin-top: 20px;
  display: grid;
}
.loadMoreButton {
  align-self: center;
  justify-self: center;
  width: 160px;
  height: 40px;
  border-radius: 20px;
  border: 3px solid linear-gradient(155deg, rgba(130,130,130,1) 25%, rgba(0,0,0,1) 75%);
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-image: linear-gradient(160deg, rgb(16, 134, 232), rgb(184, 0, 230));
  box-shadow: 0 0 0 0 #b800e6, 0.5rem 0.5rem 20px #13092d;
  cursor: pointer;
}