.container:before {
  content:"";
  position: absolute;
  right: 11px;
  top: -10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #FFFFFF transparent;
  z-index:9999;
}

.container:after {
  content: "";
  position: absolute;
  right: -33px;
  top: 25px;
  width: 0;
  rotate: 90deg;
  height: 0;
  border-style: solid;
  border-width: 0 17px 17px 17px;
  border-color: transparent transparent #ffffff transparent;
  z-index: 9998;
}

.container {
  background: #FFFFFF;
  border-radius: 10px;
  min-width: 150px;
  position: relative;
  left: -20px;
  top: 10px;
  border: 8px solid #FFFFFF;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
  float: left;
  margin: 0;
  z-index: 99999;
  text-align: left;
}
