.button {
  background: #283eaf;
  border: 1px solid #0c249d;
}

.button,
.buttonAlternative {
  color: white;
  font-family: SfUIDisplay;
  font-style: normal;
  cursor: pointer;
  min-width: 252px;
  width: fit-content;
  font-size: 20px;
  padding: 10px 6px;
  border-radius: 20px;
}

.button:hover:not(:disabled) {
  background: none;
  border: 7px solid #0c249d;
  padding: 4px 3px;
}

.button:disabled {
  filter: grayscale(1);
  cursor: default;
}

.buttonAlternative {
  background: none;
  border: none;
}

.buttonAlternative:hover {
  font-weight: bold;
}

