.titleSection {
  align-self: center;
  margin: 0px 15px 15px;
  color: whitesmoke;
}

.list {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: center;
  justify-items: center;
  margin: 0;
  margin-bottom: 28px;
  padding: 0;
}

.typeImage {
  width: 88px;
  height: 88px;
  border-radius: 50%;
  object-fit: cover;
}

.typeContainer{ 
  text-align: center;
  color: white;
  cursor: pointer;
}

@media (max-width: 900px) {
  .list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media (max-width: 600px) {
  .list {
    grid-template-columns: 1fr 1fr;
  }
}
