.container {
  display: grid;
  gap: 8px;
}

.inputContainer {
  display: flex;
  gap: 16px;
  align-items: baseline;
}

.input {
  width: -webkit-fill-available;
}

.deleteButtonContainer {
  background-color: red;
  width: 33.7px;

  font-size: 20px;
  border-radius: 120px;
  cursor: pointer;
}

.deleteButtonContainer,
.addButtonContainer {
  height: 30px;
  font-size: 20px;
  border-radius: 120px;
  cursor: pointer;
}

.addButtonContainer {
  background-color: green;
  width: 30px;
  margin: 10px 0px;
}

.buttonContainer  {
  display: inline-flex;
  font-size: 16px;
}

.boxContainer {
  max-height: 38vh;
}