.row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.container {
  gap: 10px;
  display: grid;
  margin-top: 20px;
}

.email {
  display: flex;
  align-self: center;
}

.button {
  font-size: 15px;
  width: fit-content;
  min-width: 0px;
  display: flex;
  padding: 10px 12px;
}

.buttonContainer {
  display: flex;
  justify-content: flex-end;
}
