.container {
  border: none;
  border-radius: 5px;
  color: #333;
  max-width: 300px;
  padding: 10px;
  display: grid;
  gap: 20px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  grid-template-rows: min-content;
}

.timestamp {
  text-align: right;
  font-size: 14px;
}

.description {
  text-align: justify;
  margin: 20px 0px;
}
