.logo {
  width: 286px;
  top: 50px;
  left: 50px;
}

.loginButton {
  background: white;
  color: black;
}

.loginButton,
.signUpButton {
  height: 50px;
  min-width: 0px;
  width: 180px;
}

.header {
  max-width: 100vw;
  padding: 40px 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.loginButtonContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 10px;
}

@media (max-width: 750px) {
  .header {
    max-width: 100vw;
    padding: 40px 25px;
    grid-template-columns: 1fr;
  }
  .loginButtonContainer {
    justify-content: flex-start;
    margin-top: 15px;
  }
}

