.options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 12px;
  gap: 16px;
}

.tableContainer {
  max-height: calc(100vh - 500px);
  overflow: auto;
}

.leftOption,
.rightOption {
  display: flex;
  align-items: center;
}

.leftOption {
  justify-content: left;
}

.rightOption {
  justify-content: flex-end;
}

.actions {
  display: flex;
  justify-content: flex-end;
}

.actions button {
  margin: 0px;
}

@media (max-width: 970px) {
  .options {
    grid-template-columns: 1fr;
  }

  .rightOption {
    justify-content: left;
  }
}
