.displayContainer {
  display: grid;
  width: auto;
  padding-top: 68px;
  padding-bottom: 20px;
}

.protonDisplay {
  max-height: calc(100vh - 200px);
  display: inline-flex;
  contain: content;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  overflow: auto;
  gap: 10px;
  justify-content: center;
}
