.postContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px;
  max-height: calc(100vh - 210px);
  overflow: scroll;
}

.filterContainer {
  width: 100%;
  display: grid;
  margin-bottom: 16px;
}

@media (max-width: 600px) {
  .postContainer {
    grid-template-columns: 1fr;
  }
}